import React, {Component} from "react"
import {connect} from "react-redux"

import Overlay from "../Overlay/Overlay";
import UserForm from "../FormUserInfo/Form";
import Welcometext from "./Welcome/Welcometext";


class Challenge extends Component{
    render(){
        return(
            <div>
                {this.props.userMailFormDisplayed ? (
                    <Overlay/>
                ) : null}
                {this.props.userMailFormDisplayed ? (
                    <UserForm/>
                ) : null}
                <Welcometext/>
                <div className="github">
                    Check out our github: https://github.com/LaboSecu/Welcome
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    userMailFormDisplayed: state.toggles.userEmailFormDisplayed
});

export default connect(mapStateToProps)(Challenge)