import {VALIDATE_CHALLENGE, TOGGLE_EMAIL_ERROR_POPUP, TOGGLE_ERROR_POPUP, UPDATE_PASSWORD_INPUT, UPDATE_USER_INPUT, TOGGLE_USER_EMAIL_FORM, SET_INPUT_EMAIL_FORM_ERROR} from "./actionTypes"

export function updatePasswordInput(value){
    return{
        type: UPDATE_PASSWORD_INPUT,
        payload: {
            value
        }
    }
}

export function updateUserInput(inputData){
    return {
        type: UPDATE_USER_INPUT,
        payload: inputData
    }
}

export function toggleErrorPopup(bool){
    return{
        type: TOGGLE_ERROR_POPUP,
        payload: bool
    }
}

export function toggleEmailErrorPopup(){
    return{
        type: TOGGLE_EMAIL_ERROR_POPUP
    }
}

export function toggleUserEmailForm(){
    return{
        type: TOGGLE_USER_EMAIL_FORM
    }
}

export function setEmailFormError(errorCode){
    let errorMessage;
    switch (errorCode) {
        case -1:
            errorMessage = "The provided email address' format is invalid";
            break;
        case -2:
            errorMessage = "The provided email address is not part of the YNOV organisation";
            break;
        default:
            errorMessage = "";
            break;
    }
    return{
        type: SET_INPUT_EMAIL_FORM_ERROR,
        payload: errorMessage
    }
}

export function validateChallenge(){
    return{
        type: VALIDATE_CHALLENGE
    }
}
