import {
    TOGGLE_EMAIL_ERROR_POPUP,
    TOGGLE_ERROR_POPUP,
    UPDATE_PASSWORD_INPUT,
    UPDATE_USER_INPUT,
    TOGGLE_USER_EMAIL_FORM,
    SET_INPUT_EMAIL_FORM_ERROR,
    VALIDATE_CHALLENGE
} from "../actions/actionTypes"
import {combineReducers} from "redux"

function toggles(state = {errorPopupDisplayed: false, userEmailFormDisplayed: true, emailErrorPopup: false}, action){
    switch (action.type) {
        case TOGGLE_ERROR_POPUP:
            return {
                ...state,
                errorPopupDisplayed: action.payload
            };
        case TOGGLE_USER_EMAIL_FORM:
            return {
                ...state,
                userEmailFormDisplayed: false
            };
        case TOGGLE_EMAIL_ERROR_POPUP:
            return {
                ...state,
                emailErrorPopup: !state.emailErrorPopup
            }
        default:
            return state
    }
}

function passwordReducer(state = {value: ""}, action){
    switch (action.type) {
        case UPDATE_PASSWORD_INPUT:
            return {
                ...state,
                value: action.payload.value
            };
        default:
            return state
    }
}

function userReducer(state = {userInputEmail: ""}, action){
    switch (action.type){
        case UPDATE_USER_INPUT:
            return {
                ...state,
                userInputEmail: action.payload
            };
        default:
            return state
    }
}

function errorCodesReducer(state = {emailInputError: ""}, action){
    switch (action.type) {
        case SET_INPUT_EMAIL_FORM_ERROR:
            return {
                ...state,
                emailInputError: action.payload
            };
        default:
            return state
    }
}

function challengeReducer(state = {challengeValidated: false}, action){
    switch (action.type) {
        case VALIDATE_CHALLENGE:
            return {
                ...state,
                challengeValidated: true
            };
        default:
            return state
    }
}

const reducers = combineReducers({
    password: passwordReducer,
    toggles: toggles,
    user: userReducer,
    errorMessages: errorCodesReducer,
    challenge: challengeReducer
});

export default reducers