import React, { Component } from 'react';
import {connect} from "react-redux"

import Challenge from "./components/Challenge/Challenge"
import ChallengeValidated from "./components/ChallengeValidated/ChallengeValidated"

import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
          { this.props.challengeValidated ? (<ChallengeValidated/>) : (<Challenge/>)}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
    userMailFormDisplayed: state.toggles.userEmailFormDisplayed,
    challengeValidated: state.challenge.challengeValidated
});

export default connect(mapStateToProps)(App);
