import React, {Component} from "react"
import {connect} from "react-redux"

import {updatePasswordInput, toggleErrorPopup, validateChallenge} from "../../../actions/index"

import './form.css'

const credential = "c2VrdXJpdGF5";

class Form extends Component{

    state = {
        password: "",
        popupDisplayed: false
    };

    submit = async () => {
      if(this.props.password.value === atob(credential))
      {
          this.props.validateChallenge();
      }else{
          await this.props.toggleErrorPopup(true);
          setTimeout(() => this.props.toggleErrorPopup(false), 5000);
      }
    };

    render(){
        return(
            <div id="answer-form">
                <p className="darker-text">Validation</p>
                <p className="lighter-text">Enter Password :</p>
                <div id="input-submit-row">
                    <input type="password" onChange={(e) => this.props.updatePassword(e.target.value)}/>
                    <button onClick={this.submit}><i className="fas fa-angle-right"/></button>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    password: state.password
});

const mapDispatchToProps = dispatch => ({
    updatePassword: password =>   dispatch(updatePasswordInput(password)),
    toggleErrorPopup: bool => dispatch(toggleErrorPopup(bool)),
    validateChallenge: () => dispatch(validateChallenge())
});

export default connect(mapStateToProps, mapDispatchToProps)(Form)