import React, {Component} from "react";
import {connect} from "react-redux"

import EmailErrorPopup from "../Popups/EmailErrorPopup"

import './UserForm.css';

import {updateUserInput, toggleUserEmailForm, setEmailFormError, toggleEmailErrorPopup} from "../../actions";
import {emailRegex, ynovRegex} from "../../Regexes";

class UserForm extends Component {

    checkEmail = () => {
        if (emailRegex.test(this.props.userEmail) === false){
            this.props.setErrorMessage(-1);
            return -1
        }else{
            if(ynovRegex.test(this.props.userEmail) === false){
                this.props.setErrorMessage(-2);
                return -2
            }
        }
        return 0;
    };

    submit = async () => {
        let errorCode = await this.checkEmail();

        if(errorCode === -1 || errorCode === -2) {
            this.props.toggleEmailErrorPopup()
        }else {
            await this.props.toggleUserEmailForm();
            if(this.props.errorPopupDisplayed === true){
                this.props.toggleEmailErrorPopup();
            }
        }
    };

    render(){
        return(
            <div id={"userForm-wrapper"}>
                <p>Before starting the challenge, please enter your YNOV email address</p>
                <input type={"email"} value={this.props.userEmail} onChange={(e) => this.props.updateUserEmail(e.target.value)}/>
                <button className={"submit-button"} onClick={this.submit}>Submit</button>
                <EmailErrorPopup/>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    userEmail: state.user.userInputEmail,
    errorMessage: state.errorMessages.emailInputError,
    errorPopupDisplayed: state.toggles.emailErrorPopup
});

const mapDispatchToProps = dispatch => ({
    updateUserEmail: inputData => dispatch(updateUserInput(inputData)),
    setErrorMessage: errorCode => dispatch(setEmailFormError(errorCode)),
    toggleUserEmailForm : () => dispatch(toggleUserEmailForm()),
    toggleEmailErrorPopup: () => dispatch(toggleEmailErrorPopup())
});

export default connect(mapStateToProps, mapDispatchToProps)(UserForm);