import React, {Component} from "react"
import {connect} from "react-redux";

import './EmailErrorPopup.css'

class EmailErrorPopup extends Component {
    render(){
        return(
            <div className={this.props.togglePopup ? "dropDown-popup-visible" : "dropDown-popup-invisible"}>
                {this.props.errorMessage}
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    errorMessage: state.errorMessages.emailInputError,
    togglePopup: state.toggles.emailErrorPopup
});

export default connect(mapStateToProps)(EmailErrorPopup)